.sectionGames{
    margin: 5% 9%;
    display:flex;
    flex-direction: column;
    align-items: center;
}

.sectionGames h2 {
    text-align: center;
    font-size: 5vmin;
    font-family: 'Fredoka One', cursive;
    color: rgb(136,240,192);
    text-shadow: -1px 2px 0 #389067;
    letter-spacing: 4px;
    margin: 50px 0;
}

.articleGames {
    display: flex;
    justify-content: center;
    align-items: center;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5vmin;
}

.divGame{
    display: flex;
    justify-content: flex-start;
    background-color: rgb(109,98,231);
    border-radius: 10px;
    display: grid;
    grid-template-columns: 40% 50%;
    gap: 3vmin;
    box-shadow: 0 5px 16px 0 rgb(0 0 0 / 25%);
    padding: 2vmin;
}

.picGame{
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 20px;
    height: 100%
}

.descriptionGame{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3vmin;
}


.titleDescription{
    text-transform: uppercase;
    font-family: 'Fredoka One', cursive;
    color: rgb(245,209,35);
    letter-spacing: 2px;
}

.subtitleDescription{
    text-transform: uppercase;
    font-family: 'Fredoka One', cursive;
    color: rgb(225,223,255);
    letter-spacing: 1.5px;
    font-weight: 200;
    font-size: 1.3vw;
}
.pDescription{
    font-family: 'Ubuntu', sans-serif;
    color: rgb(225,223,255);
}
.buttonDescription{
    font-family: 'Fredoka One', cursive;
    text-transform: uppercase;
    color: rgb(109,98,231);
    background-color: rgb(225,223,255);
    border:none;
    width: 15vw;
    border-radius: 8px;
    font-size: 2.8vmin;
    padding: 2vmin 0;
}

@media (min-width: 916px) and (max-width: 1100px) {
    .articleGames {
        grid-template-columns: 70%;
    }
        
  .sectionGames h2 {
    font-size: 4vw;
  }
  .titleDescription {
    font-size: 3vw;
  }
  .subtitleDescription {
    font-size: 2vw;
  }
  .buttonDescription {
    width: 25vw;
    font-size: 2vw;
  }
}

@media (min-width: 768px) and (max-width: 916px) {
    .articleGames {
        grid-template-columns: 90%;
    }
    
  .sectionGames h2 {
    font-size: 4.5vw;
  }
  .titleDescription {
    font-size: 3.5vw;
  }
  .subtitleDescription {
    font-size: 2.5vw;
  }
  .buttonDescription {
    width: 25vw;
    font-size: 2.5vw;
  }
}

@media (min-width: 550px) and (max-width: 768px) {
    .articleGames {
        grid-template-columns: 100%;
    }

  .sectionGames h2 {
    font-size: 4.5vw;
  }
  .titleDescription {
    font-size: 4vw;
  }
  .subtitleDescription {
    font-size: 2.3vw;
  }
  .buttonDescription {
    width: 25vw;
    font-size: 2.7vw;
  }

  .pDescription {
    font-size: 2.4vw;
  }
}

@media (max-width: 550px){

    .articleGames {
        grid-template-columns: 100%;
    }
    .sectionGames h2{
    font-size: 6vw;
  }
  .titleDescription {
    font-size: 3vw;
    text-align: center;
  }

  .subtitleDescription {
    font-size: 2.5vw;
  }

  .buttonDescription {
    min-width: 25vw;
    font-size: 2.5vw;
    align-self: center;
  }
  .pDescription {
    font-size: 2.3vw;
  }
}